	import service from "../../service.js";
	import headers from "../../components/header_nav.vue"
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore,
			headers
		},
		data() {
			return {
				Htitle: "预约列表",
				Hlicon: "../../static/icon/left_whitch.png",
				merchid:uni.getStorageSync("merchid"),
				user:uni.getStorageSync("member"),
				index: 1,
				navlist: [{
						id: 1,
						title: "处理中"
					},
					{
						id: 2,
						title: "已处理"
					}
				],
				appoinlist:[],//预约列表
				isLoading:false,
				scroll:false,
				status:0,
				pages:1,
				page:0,
				out:0,
			}
		},
		onShow:function(){
			this.loadHistory();
		},
		onReachBottom() {
			this.loadHistory();
		},
        methods: {
			cancelConsent(id , e){//取消or同意
			var self = this;
			uni.showModal({
			    title: '提示',
			    content: '确认执行此操作吗?',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.request({
							url:self.$service.api.service.sub_agree,
							method:"POST",
							data:{
								id:id,
								status:e
							},
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
							success:(res)=>{
								if(res.data.code == 1){
									uni.showToast({
										title:res.data.info,
										icon:"none"
									});
									self.appoinlist = [];
									self.isLoading = false;
									self.scroll = false;
									self.status = 0;
									self.pages = 1;
									self.page = 0;
									self.loadHistory();
								}else{
									uni.showToast({
										title:res.data.info,
										icon:"none"
									});
								}
							},
							fail: (res) => {
								uni.showToast({
									title:res.data.info,
									icon:"none"
								});
							}
							
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			},
			
			//加载历史数据
			loadHistory(e){
				if(this.isLoading){
					return ;
				}
				if(this.page < this.pages){
					this.out = 0;
					this.isLoading = true;
					this.scroll = false;
					this.status = 1;
					var page = this.page+1;
					service.auth(this, service.api.service.appoinlist, 'GET', {
						'page':page,
						// 'mid':this.user.mid,
						'merch_id':this.merchid,
						'status': this.index
					}, function(self, res) {
						if (res.code == 1) {
							var list = res.data.data;
							for(let i=0;i<list.length;i++){
								//将数据逐条写入数组
								self.appoinlist.push(list[i]);
							}
							self.pages = res.data.page_total;
							self.page = page;
							self.scroll = true;
							self.isLoading = false;
							/* setTimeout(function(){ */
								self.out = 1;
							/* },1000); */
							uni.setStorageSync('tasktime',1);
							if(self.page == self.pages || res.data.total == 0){
								self.status = 2;
							}else{
								self.status = 0;
							}
						}
					});
				}else{
					this.status = 2;
				}
			},
			selnav(e){
				this.appoinlist = [];
				this.isLoading = false;
				this.scroll = false;
				this.status = 0;
				this.pages = 1;
				this.page = 0;
				this.index = e;
				this.loadHistory();
			},
		},
		onShow() {
			this.loadHistory();
		}
	}
